                <div class="input-form-wrapper">
                    <div class="input-form">
                        <v-select placeholder="  ." v-model="selectedUserType" :value="this.selectedUserType" @input="selectedUser" :options="this.userOptions" :reduce="options => options.code" label="label"></v-select>
                        <div class="notice">
                            <span v-show="this.selectedUserType === 'U'"><strong>        .</strong></span>
                            <span v-show="this.selectedUserType !== 'U'"><strong>    ajou.ac.kr  .</strong></span>
