                    </div>
                    <div class="input-form" v-if="this.selectedUserType === 'R' || this.selectedUserType === 'G'">
                        <div class="input-group">
                            <v-select :class="{ 'error': this.errorValidation.college }" placeholder=" ." v-model="selectedCollege" :value="this.selectedCollege" @input="selectedCollegeCd" :options="this.collegeList" :reduce="college => college.college_cd" label="college_nm"></v-select>
                            <p class="auto-validate-noti" :class="{ 'error': this.errorValidation.college }" v-if="this.errorValidation.college">{{ this.errorMsg.college }}</p>
                        </div>
                        <div class="input-group">
                            <v-select :class="{ 'error': this.errorValidation.dpt }" v-if="this.selectedCollege" placeholder=" ." v-model="selectedDpt" :value="this.selectedDpt" @input="selectedDptCd" :options="this.dptList" :reduce="dpt => dpt.dpt_cd" label="dpt_nm"></v-select>
                            <p class="auto-validate-noti" :class="{ 'error': this.errorValidation.dpt }" v-if="this.errorValidation.dpt">{{ this.errorMsg.dpt }}</p>
                            <div class="input-form input-form-horizontal" v-if="this.selectedCollege && this.selectedDpt">
                                <label for="smajor" role="title" class="input-form-title flex-9">2(/) .</label>
                                <div class="input-form radio-wrapper flex-1">
                                    <input type="checkbox" name="smajor" id="smajor" v-model="hasSubMajor" />
                                </div>
                            </div>
                            <div class="input-group">
                                <v-select :class="{ 'error': this.errorValidation.subCollege }" v-if="this.hasSubMajor" placeholder=" ." v-model="selectedSubCollege" :value="this.selectedSubCollege" @input="selectedSubCollegeCd" :options="this.collegeList" :reduce="college => college.college_cd"
                                    label="college_nm"></v-select>
                                <p class="auto-validate-noti" :class="{ 'error': this.errorValidation.subCollege }" v-if="this.errorValidation.subCollege">{{ this.errorMsg.subCollege }}</p>
                            </div>
                            <div class="input-group">
                                <v-select :class="{ 'error': this.errorValidation.subDpt }" v-if="this.hasSubMajor && this.selectedSubCollege" placeholder=" ." v-model="selectedSubDpt" :value="this.selectedSubDpt" @input="selectedSubDptCd" :options="this.dptSubList" :reduce="dpt => dpt.dpt_cd"
                                    label="dpt_nm"></v-select>
                                <p class="auto-validate-noti" :class="{ 'error': this.errorValidation.subDpt }" v-if="this.errorValidation.subDpt">{{ this.errorMsg.subDpt }}</p>
                            </div>
                        </div>
                    </div>
                    <div class="input-form">
                        <input type="text" :class="{ 'error': this.errorValidation.user_nm }" v-model="userName" placeholder="" required>
                        <p class="auto-validate-noti" :class="{ 'error': this.errorValidation.user_nm }" v-if="this.errorValidation.user_nm">{{ this.errorMsg.user_nm }}</p>
                    </div>
                    <div class="input-form">
                        <input name="IDNum" :class="{ 'error': this.errorValidation.user_st_id }" :disabled="this.selectedUserType === 'U' || this.selectedUserType === 'E'" v-model="userIDNum" type="text" maxlength="9" placeholder="" required pattern="[0-9]{9,}">
                        <p class="auto-validate-noti" :class="{ 'error': this.errorValidation.user_st_id }" v-if="this.errorValidation.user_st_id">{{ this.errorMsg.user_st_id }}</p>
                    </div>
                    <div class="input-form">
                        <input name="email" :class="{ 'error': this.errorValidation.email }" v-model="email" type="email" autocapitalize="none" placeholder=" ( @ajou.ac.kr )" required>
                        <p class="auto-validate-noti" v-if="this.email && this.validatedEmail && !this.errorValidation.email">  .</p>
                        <p class="auto-validate-noti" :class="{ 'error': this.errorValidation.email }" v-if="this.errorValidation.email">{{ this.errorMsg.email }}</p>
                    </div>
                    <div class="input-form">
                        <input name="userID" type="text" placeholder="" @blur="checkDupID" @keyup.delete="initError('user_id')" :class="{ 'error': this.errorValidation.user_id }" required v-model="userID" autocapitalize="none" pattern="[0-9A-Za-z_]{6,}">
                        <p class="auto-validate-noti" v-if="this.userID && this.validatedUserID && !this.errorValidation.user_id">  .</p>
                        <p class="auto-validate-noti" :class="{ 'error': this.errorValidation.user_id }" v-if="this.errorValidation.user_id">{{ this.errorMsg.user_id }}</p>
                    </div>
                    <div class="input-form">
                        <div class="input-group">
                            <input name="password" v-model="password" type="password" autocapitalize="none" pattern=".{8,}" placeholder="" required :class="{ 'error': this.errorValidation.user_pw }">
                            <p class="auto-validate-noti" :class="{ 'error': this.errorValidation.user_pw }" v-if="this.errorValidation.user_pw">{{ this.errorMsg.user_pw }}</p>
                        </div>
                        <div class="input-group">
                            <input name="passwordConfirm" v-model="passwordConfirm" type="password" autocapitalize="none" pattern=".{8,}" placeholder=" " required :class="{ 'error': this.errorValidation.user_pw_confirm }">
                            <p class="auto-validate-noti" v-if="this.passwordConfirm && this.validatedPWConfirm && !this.errorValidation.user_pw_confirm">  .</p>
                            <p class="auto-validate-noti" :class="{ 'error': this.errorValidation.user_pw_confirm }" v-if="this.errorValidation.user_pw_confirm">{{ this.errorMsg.user_pw_confirm }}</p>
