<template>
    <div class="wrapper">
        <Navigation :scrollBase="scrollBase" />
        <Landing ref="scrollBase" title="//" description="     ." background="http://www.ajou.ac.kr/_attach/new/_images/2019/12/23/191223_main_visual01.jpg" />
        <div class="container">
            <article>
                <header class="underline underline-inline-block"> </header>
                <section>
                    <form data-post-form @submit.prevent>
                        <div class="input-form">
                            <label for="name"></label>
                            <input type="text" v-model="name" name="name" id="name" placeholder=" " required pattern=".{1,}" />
                        </div>
                        <div class="input-form">
                            <label for="email"></label>
                            <div class="input-form-group">
                                <input type="email" v-model="email" name="email" id="email" placeholder="  " required :class="{ 'error': this.emailError }" />
                                <p class="auto-validate-noti" :class="{ 'error': this.emailError }" v-if="this.emailError">{{ this.emailErrorMsg }}</p>
                            </div>
                        </div>
                        <div class="input-form editor">
                            <label for="textarea"></label>
                            <div class="input-form-group">
                                <ckeditor name="textarea" :editor="editor" v-model="editorData" :config="editorConfig"></ckeditor>
                                <p class="auto-validate-noti" :class="{ 'error': this.editorError }" v-if="this.editorError">{{ this.editorErrorMsg }}</p>
                            </div>
                        </div>
                        <div class="input-form-controls">
                            <input type="submit" class="btn btn-submit text-inverse" value=" " @click="contact">
                        </div>
                    </form>
                </section>
            </article>
        </div>
        <Footer />
    </div>
</template>

<script>
import Navigation from '@/components/Navigation.vue'
import Landing from '@/components/Landing.vue'
import Footer from '@/components/Footer.vue'
import CKEditor from '@ckeditor/ckeditor5-vue'
import ClassicEditor from '@ckeditor/ckeditor5-build-classic'
export default {
    name: 'contact',
    components: {
        Navigation, Landing, Footer
    },
    data () {
        return {
            scrollBase: null,
            name: '',
            editor: ClassicEditor,
            editorData: '',
            editorConfig: {
                toolbar: []
            },
            editorError: false,
            editorErrorMsg: '',
            email: '',
            emailError: false,
            emailErrorMsg: ''
        }
    },
    mounted () {
        this.scrollBase = this.$refs.scrollBase.$el.getBoundingClientRect().bottom / 3
    },
    watch: {
        email(value) {
            let re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,24}))$/
            if (value) {
                if (!re.test(value)) {
                    this.emailError = true
                    this.emailErrorMsg = '  .'
                } else {
                    this.emailError = false
                    this.emailErrorMsg = ''
                }
            } else {
                this.emailError = false
                this.emailErrorMsg = ''
            }
        },
        editorData(value) {
            if (!value) {
                this.editorError = true
                this.editorErrorMsg = ' .'
            } else {
                this.editorError = false
                this.editorErrorMsg = ''
            }
        }
    },
    methods: {
        contact () {
            if (this.name && (this.email && !this.emailError)) {
                if (this.editorData) {
                    this.$swal({
                        title: '!',
                        text: '  .',
                        footer: '<p>       .</p>',
                        type: 'success',
                        width: '90vw'
                    }).then(() => {
                        window.location = '/contact'
                    })
                } else {
                    this.$swal({
                        title: '!',
                        text: ' .',
                        footer: '<p>    .</p>',
                        type: 'error',
                        width: '90vw'
                    })
                }
            } else {
                this.$swal({
                    title: '!',
                    text: '  .',
                    footer: '<p>    .</p>',
                    type: 'error',
                    width: '90vw'
                })
            }
        }
    }
}
</script>

<style lang="scss">

article {
    margin-top: 2rem !important;
    padding: 0 2rem !important;
    & header {
        font-weight: bold;
        font-size: 1.5rem;
        & span {
            position: relative;
            &::before {
                content: "";
                display: block;
                position: absolute;
                left: 0;
                z-index: -1;
                bottom: 1px;
                height: 10px;
                background-color: #667eea;
                transition: all .4s cubic-bezier(0.57, 0.02, 0.06, 1);
                width: 100%;
            }
        }
    }
}
</style>