<template>
    <Error :code="code" :description="description" :sub_description="sub_description" />
</template>

<script>
import Error from '@/components/Error.vue'
export default {
  name: 'error_by_code',
  components: {
      Error
  },
  data () {
    return {
        errData: {
            '401': {
                title: '     .',
                description: '     .'
            },
            '403': {
                title: '   .',
                description: '      .'
            },
            '404': {
                title: '   .',
                description: '    .'
            },
            '405': {
                title: '    .',
                description: '  .'
            },
            '500': {
                title: '  .',
                description: '      .'
            },
            '503': {
                title: '   .',
                description: '   .'
            }
        }
    }
  },
  computed: {
      code () {
          if (this.$route.params.code in this.errData) {
              return this.$route.params.code
          } else {
              window.location = '/error/404'
          }
      },
      description () {
          return this.errData[this.$route.params.code]['title']
      },
      sub_description () {
          return this.errData[this.$route.params.code]['description']
      }
  }
}
</script>